$(function(){

    $('.main_slick').slick({
        lazyLoad: 'ondemand',
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        speed: 2000,
        autoplaySpeed: 3000,
        respondTo: 'min',
        pauseOnDotsHover: true
    });

    var $menu_start = $('.menu_start');

    $('.mobile_btn1').on('click', function(){
        var $menu_start = $(this).next().stop(true).slideToggle(500);
		$navigation_start.slideUp(500);
		event.stopPropagation ? event.stopPropagation() : (event.cancelBubble=true);
    });

    var $navigation_start = $('.navigation_start');

    $('.mobile_btn2').on('click', function(){
        var $navigation_start = $(this).next().stop(true).slideToggle(500);
		$menu_start.slideUp(500);
		event.stopPropagation ? event.stopPropagation() : (event.cancelBubble=true);
    });
	
	$('.mobile_home').on('click', function(){
        $menu_start.slideUp(500);
        $navigation_start.slideUp(500);
		event.stopPropagation ? event.stopPropagation() : (event.cancelBubble=true);
    });



    var $menu = $('.mobile_menu');
    $menu.attr('check', '0');

    $(window).on('scroll', function(){

        var scrollTop = $(this).scrollTop();
        var $content = $('.menu');
        if($(window).width() > 720) {
            if (($menu.attr('check') == '0') && scrollTop > 190) {
                $menu.attr('check', '1');
                $menu.css({
                    "position": "fixed",
                    "top": "-9px"
                });
                $content.css({
                    "padding-top": "95px"
                });
            }
            else if (($menu.attr('check') == '1') && scrollTop <= 190) {
                $menu.attr('check', '0');
                $menu.css({
                    "position": "absolute",
                    "top": "165px"
                });
                $content.css({
                    "padding-top": "25px"
                });
            }
            else {
                $menu_start.slideUp(500);
                $navigation_start.slideUp(500);
            }
        }
        else{
            if (($menu.attr('check') == '0') && scrollTop > 205) {
                $menu.attr('check', '1');
                $menu.css({
                    "position": "fixed",
                    "top": "-9px"
                });
                $content.css({
                    "padding-top": "95px"
                });
            }
            else if (($menu.attr('check') == '1') && scrollTop <= 205) {
                $menu.attr('check', '0');
                $menu.css({
                    "position": "absolute",
                    "top": "216px"
                });
                $content.css({
                    "padding-top": "25px"
                });
            }
            else {
                $menu_start.slideUp(500);
                $navigation_start.slideUp(500);
            }
        }
    });
	var $html = $('html');
	
	$html.on('click', function(){
		$menu_start.slideUp(500);
        $navigation_start.slideUp(500);
	});
	
    $(window).resize(function(){
        if($(window).width() > 720){
            $menu.css({
                "top": "170px"
            });
        }
        else{
            $menu.css({
                "top": "216px"
            });
        }
    });
    $(window).on("resize", function(){
        if($(window).width() > 900){
            $menu_start.slideUp(500);
            $navigation_start.slideUp(500);
        }
    });
	
	var $searchINP = $('.header_search--search');
	var $searchBTN = $('.header_search--submit');
	
	$searchINP.on('focus', function(){
		$searchINP.css({
                "box-shadow": "7px 7px 15px 0px #ca65d8, -7px -7px 15px #ca65d8, -6px 8px 15px 0px #ca65d8"
            });
		$searchBTN.css({
                "box-shadow": "9px 9px 15px 0px #ca65d8, 7px -7px 15px #ca65d8"
            });
	});
	$searchINP.on('blur', function(){
		$searchINP.css({
                "box-shadow": "none"
            });
		$searchBTN.css({
                "box-shadow": "none"
            });
	});
	
	var $mainBaner = $('.main__banner--li');
	
	$mainBaner.mouseenter(function(){
	    $(this).css({ 
			"box-shadow": "11px 11px 35px 0px #ca65d8, -11px 11px 35px 0px #ca65d8, -11px -11px 35px 0px #ca65d8, 11px -11px 35px 0px #ca65d8"
		});
	});
	$mainBaner.mouseleave(function(){
	    $(this).css({ 
			"box-shadow": "none"
		});
	});
	
	var $nav2a = $('.header_nav2--a');

	$nav2a.mouseenter(function(){
	    $(this).css({ 
			"color": "black",
			'font-weight': 'bold',
			'text-transform' : 'uppercase'
		});
	});
	$nav2a.mouseleave(function(){
	    $(this).css({ 
			"color": "white",
			'font-weight': 'normal',
			'text-transform' : 'none'
		});
	});
	
});


